.top-info-container > .info-container {
    align-self: stretch;
    box-sizing: border-box;
    align-content: flex-end;
}
.top-info-container {
    display: flex;
    flex-direction: row;
    gap: 32px;
    justify-content: flex-start;
    align-items: flex-start;
    width: 100%;
    margin-bottom: 18px;
}
/* Info container main style */
.info-container {
    background: #f8fbff;
    border: 1.5px solid #e0eaff;
    border-radius: 10px;
    padding: 18px 22px;
    margin-bottom: 18px;
    box-shadow: 0 2px 8px rgba(0,123,255,0.06);
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 8px;
}
/* Info container styles */
.info-group {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    width: 100%;
    margin-top: 10px;
    margin-bottom: 6px;
    gap: 15px;
}

.info-label {
    width: 150px;
    min-width: 150px;
    max-width: 150px;
    text-align: left;
    font-weight: 500;
    color: #1a3a5d;
    font-size: 1.05em;
    padding-right: 8px;
    flex-shrink: 0;
}

.info-value {
    font-weight: bold;
    color: #007bff;
    font-size: 1.08em;
    letter-spacing: 0.2px;
    text-align: left;
    padding-left: 4px;
    min-width: 60px;
    max-width: 100px;
    display: inline-block;
}
#starting-params-container {
    display: flex;
    flex-direction: column;
    gap: 14px;
    align-items: flex-start;
    justify-content: flex-start;
    margin-bottom: 24px;
    width: 100%;
    max-width: 350px;
    min-width: 260px;
}
#starting-params-container .info-group {
    width: 100%;
    justify-content: flex-start;
    align-items: center;
    margin-bottom: 6px;
}
#starting-params-container .info-label {
    width: 150px;
    min-width: 150px;
    max-width: 150px;
    text-align: left;
    font-weight: 500;
    color: #1a3a5d;
    font-size: 1.05em;
    padding-right: 8px;
    flex-shrink: 0;
}
#starting-params-container .info-value {
    font-weight: bold;
    color: #007bff;
    font-size: 1.08em;
    letter-spacing: 0.2px;
    text-align: left;
    padding-left: 4px;
    min-width: 60px;
    max-width: 100px;
    display: inline-block;
}

.filter-container {
    max-width: 1200px;
    min-width: 750px;
    margin: 32px auto 0 auto;
    background: linear-gradient(135deg, #f8fbff 0%, #e3f0ff 100%);
    border-radius: 18px;
    box-shadow: 0 8px 32px rgba(0, 123, 255, 0.10), 0 1.5px 8px rgba(0, 0, 0, 0.04);
    padding: 32px 24px 40px 24px;
    border: 1.5px solid #e0eaff;
    position: relative;
    overflow: hidden;
    transition: box-shadow 0.2s;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.filter-container:hover {
    box-shadow: 0 12px 40px rgba(0, 123, 255, 0.16), 0 2px 12px rgba(0, 0, 0, 0.06);
}

.filter-container h2 {
    text-align: center;
    font-size: 2.1em;
    font-weight: 700;
    color: #1a3a5d;
    margin-bottom: 28px;
    letter-spacing: 1px;
    text-shadow: 0 2px 8px #e0eaff;
}

.input-field {
    padding: 10px 14px;
    border: 2px solid #007bff;
    border-radius: 6px;
    font-size: 1.1em;
    background: #f8fbff;
    color: #222;
    outline: none;
    transition: border-color 0.2s, box-shadow 0.2s;
    box-shadow: 0 2px 8px rgba(0, 123, 255, 0.08);
    margin-bottom: 8px;
    min-width: 30px;
    max-width: 50px;
}

.text-input-field {
    padding: 10px 14px;
    border: 2px solid #007bff;
    border-radius: 6px;
    font-size: 1.1em;
    background: #f8fbff;
    color: #222;
    outline: none;
    transition: border-color 0.2s, box-shadow 0.2s;
    box-shadow: 0 2px 8px rgba(0, 123, 255, 0.08);
    margin-bottom: 8px;
}

.input-field:focus {
    border-color: #0056b3;
    box-shadow: 0 0 0 2px #cce4ff;
}

.input-button {
    padding: 10px 20px;
    border: none;
    border-radius: 6px;
    color: #fff;
    font-size: 1.1em;
    font-weight: 500;
    cursor: pointer;
    box-shadow: 0 2px 8px rgba(0, 123, 255, 0.12);
    transition: background 0.2s, box-shadow 0.2s;
}

.input-button:hover {
    background: linear-gradient(90deg, #0056b3 0%, #007bff 100%);
    box-shadow: 0 4px 16px rgba(0, 123, 255, 0.18);
}

.responsive-flex {
    display: flex;
    flex-wrap: wrap;
    gap: 32px;
    justify-content: center;
    align-items: flex-start;
}

@media (max-width: 900px) {
    .responsive-flex {
        flex-direction: column;
        gap: 16px;
        align-items: stretch;
    }

    .list-container {
        width: 100%;
        height: 100%;
        min-width: 0;
        margin: 0 auto;
    }

    .top-input-container {
        flex-direction: column;
        gap: 12px;
        align-items: stretch;
    }
}

@media (max-width: 600px) {
    body {
        font-size: 0.95em;
    }

    .list-item {
        font-size: 1em;
        min-width: 24px;
        min-height: 24px;
        padding: 2px 6px;
    }

    .responsive-flex {
        gap: 8px;
    }
}

