}

.side-by-side-container {
    display: inline-flex;
    margin-top: 20px;
    justify-content: center;
    margin: 10px;
    gap: clamp(70px, 25vw, 400px);
}

.filter-elements {
    display: flex;
    flex-direction: column;
    gap: 8px;
    justify-content: center;
    align-items: center;
}

